let pagina = 1;

const cita = {
    nombre: '',
    fecha: '',
    hora: '',
    servicios: []
}
document.addEventListener('DOMContentLoaded', function() {
    starApp();
});

function starApp() {
    showServices();
    //resalta el div actual segun el tag que se presiona

    showSection();

    //oculta o muestra una seccion segun el tab al que se presiona
    changeSection();

    //paginacion siguiente yanterior

    pageNext();
    pagePrev();
    //comprueba pagina actual para ocultar o mostrar la sigueinte
    buttonsPage();

    //muestra el resumen de la cita o error
    showResume();
    //almacena el nombre de la cita
    nameA();

    //alamacena fecha
    date();
    disableDatePrev();
    //almacena la hora de la cita del objeto
    horaCita();
}

function showSection() {
    //eliminar mostrar -seccion de la seccion anteriot
    const seccionAnt = document.querySelector('.mostrar-seccion');
    if (seccionAnt) {
        seccionAnt.classList.remove('mostrar-seccion');
    }


    const seccionActual = document.querySelector(`#paso-${pagina}`);
    seccionActual.classList.add('mostrar-seccion');

    //eliminar la clase actual en el tabs
    const tabAnt = document.querySelector('.tabs .actual');
    if (tabAnt) {
        tabAnt.classList.remove('actual');
    }

    //resalta el tab actual
    const tab = document.querySelector(`[data-paso="${pagina}"]`);
    tab.classList.add('actual');


}

function changeSection() {
    const enlaces = document.querySelectorAll('.tabs button');
    enlaces.forEach(enlace => {
        enlace.addEventListener('click', e => {
            e.preventDefault();
            pagina = parseInt(e.target.dataset.paso);



            //agregar mostrar-seccion donde dimos click
            /*   const seccion = document.querySelector(`#paso-${pagina}`);
              seccion.classList.add('mostrar-seccion');

              //eliminar la clase actual en el tab
              document.querySelector('.tabs .actual').classList.remove('actual');
              //agregar la clase actual en el nuevo tab
              const tab = document.querySelector(`[data-paso="${pagina}"]`);
              tab.classList.add('actual'); */

            showSection();
            buttonsPage();
        })
    })
}

async function showServices() {
    try {
        const resultado = await fetch('./servicios.json');
        const db = await resultado.json();

        const { servicios } = db;
        //generar html
        servicios.forEach(servicio => {
            const { id, nombre, precio } = servicio;
            //DOM Scripting
            //nombre del servcio
            const nombreServicio = document.createElement('P');
            nombreServicio.textContent = nombre;
            nombreServicio.classList.add('nombre-servicio');

            //precio
            const precioServicio = document.createElement('P');
            precioServicio.textContent = `$ ${precio}`;
            precioServicio.classList.add('precio-servicio');

            //Generar DIV contenedor de Servicios
            const serviciosDiv = document.createElement('DIV');
            serviciosDiv.classList.add('servicio');
            serviciosDiv.dataset.idServicio = id;
            //selecciona un servicio para la cita
            serviciosDiv.onclick = seleccionarServicio;


            //inyectar precioy nombre al div 
            serviciosDiv.appendChild(nombreServicio);
            serviciosDiv.appendChild(precioServicio);
            //inyectarlo en el html
            document.querySelector('#servicios').appendChild(serviciosDiv);
        });
    } catch (error) {
        console.log(error);
    }
}


function seleccionarServicio(e) {
    let elemento;
    //forzar que el elemento al cual le damos click sea el div
    if (e.target.tagName === 'P') {
        elemento = e.target.parentElement;
    } else {
        elemento = e.target;
    }
    if (elemento.classList.contains('seleccionado')) {
        elemento.classList.remove('seleccionado');

        const id = parseInt(elemento.dataset.idServicio);

        eliminarServ(id);
    } else {
        elemento.classList.add('seleccionado');

        const servicioObj = {
            id: parseInt(elemento.dataset.idServicio),
            nombre: elemento.firstElementChild.textContent,
            precio: elemento.firstElementChild.nextElementSibling.textContent,

        }
        agregarServ(servicioObj);
    }

}

function eliminarServ(id) {
    const { servicios } = cita;
    cita.servicios = servicios.filter(servicio => servicio.id !== id);

}

function agregarServ(servicioObj) {
    const { servicios } = cita;
    cita.servicios = [...servicios, servicioObj];

}

function pageNext() {
    const pageNext = document.querySelector('#siguiente');
    pageNext.addEventListener('click', () => {
        pagina++;
        buttonsPage();
    });
}

function pagePrev() {
    const pagePrev = document.querySelector('#anterior');
    pagePrev.addEventListener('click', () => {
        pagina--;
        buttonsPage();
    });
}

function buttonsPage() {
    const pageNext = document.querySelector('#siguiente');
    const pagePrev = document.querySelector('#anterior');
    if (pagina === 1) {
        pagePrev.classList.add('ocultar');
        pageNext.classList.remove('ocultar');
    } else if (pagina === 3) {
        pageNext.classList.add('ocultar');
        pagePrev.classList.remove('ocultar');
        showResume(); //estamos en la pagina 3 carga el resumen de la cita
    } else {
        pagePrev.classList.remove('ocultar');
        pageNext.classList.remove('ocultar');
    }
    showSection();
    
}

function showResume() {
   
    //destructuring
    const { nombre, fecha, hora, servicios } = cita;

    //selecionar el resumen

    const resumenDiv = document.querySelector('.contenido-resumen');
    
    //limpia el html
    while (resumenDiv.firstChild) {
        resumenDiv.removeChild(resumenDiv.firstChild);

    }

    //validacion de objeto
    if (Object.values(cita).includes('')) {
        const noServ = document.createElement('P');
        noServ.textContent = 'Faltan datos de Servicios, hora, fecha o nombre';
        noServ.classList.add('invalidar-cita');
        //agregar a resumen div

        resumenDiv.appendChild(noServ);
        return;
    }
    //heading cita
    const headingCita = document.createElement('H3');
    headingCita.textContent = 'Resumen de Cita';

    //mostrar resumen
    const name = document.createElement('P');
    name.innerHTML = `<span>Nombre:</span> ${nombre}`;


    const date = document.createElement('P');
    date.innerHTML = `<span>Fecha:</span> ${fecha}`;


    const horaCita = document.createElement('P');
    horaCita.innerHTML = `<span>Hora:</span> ${hora}`;


    const serviciosCita = document.createElement('DIV');
    serviciosCita.classList.add('resumen-servicio')

    const headingServ = document.createElement('H3');
    headingServ.textContent = 'Resumen de Servicio';

    serviciosCita.appendChild(headingServ);
    let cantidad = 0;
    // iterar sobre el arreglo de servicios
    servicios.forEach(servicio => {

        const { nombre, precio } = servicio;
        const contenedorServ = document.createElement('DIV');
        contenedorServ.classList.add('contenedor-servicio');

        const textoServ = document.createElement('P');
        textoServ.textContent = nombre;

        const precioServ = document.createElement('P');
        precioServ.textContent = precio;
        precioServ.classList.add('precio');

        const totalServ = precio.split('$');
        cantidad += parseInt(totalServ[1].trim());
        //colocar texto y precio 3en el div
        contenedorServ.appendChild(textoServ);
        contenedorServ.appendChild(precioServ);
        serviciosCita.appendChild(contenedorServ);
    });
    resumenDiv.appendChild(headingCita);
    resumenDiv.appendChild(name);
    resumenDiv.appendChild(date);
    resumenDiv.appendChild(horaCita);
    resumenDiv.appendChild(serviciosCita);

    const cantidadPagar = document.createElement('P');
    cantidadPagar.classList.add('total');
    cantidadPagar.innerHTML = `<span>Total a Pagar: </span> $ ${cantidad}`;
    resumenDiv.appendChild(cantidadPagar);
}

function nameA() {
    const nombreInput = document.querySelector('#nombre');
    nombreInput.addEventListener('input', e => {
        const nombreTexto = e.target.value.trim(); //trim elimina el espacio en blanco al final y al principio

        //validacion nombretexto tiene que tener algo
        if (nombreTexto === '' || nombreTexto.length < 3) {
            mostrarAlerta('Nombre no valido', 'error');
        } else {
            const alerta = document.querySelector('.alerta');
            if (alerta) {
                alerta.remove();
            }
            cita.nombre = nombreTexto;

        }

    });
}

function mostrarAlerta(mensaje, tipo) {

    //si hay una alerta previe, entonces no crear otra
    const alertaPrev = document.querySelector('.alerta');
    if (alertaPrev) {
        return;
    }

    const alerta = document.createElement('DIV');
    alerta.textContent = mensaje;
    alerta.classList.add('alerta');
    if (tipo === 'error') {
        alerta.classList.add('error');
    }
    //insertar en html
    const formulario = document.querySelector('.formulario');
    formulario.appendChild(alerta);

    //eliminar la alaerta despues de 3 seg
    setTimeout(() => {
        alerta.remove();
    }, 3000);
}

function date() {
    const fechaInput = document.querySelector('#fecha');
    fechaInput.addEventListener('input', e => {
        const dia = new Date(e.target.value).getUTCDay();
        if ([0, 6].includes(dia)) {
            e.preventDefault();
            fechaInput.value = '';
            mostrarAlerta('Fin de semana no son permitidos', 'error')
        } else {
            cita.fecha = fechaInput.value;
            
        }


    });
}

function disableDatePrev() {
    const inputFecha = document.querySelector('#fecha');
    const fechaAhora = new Date();
    const year = fechaAhora.getFullYear();
    const mes = fechaAhora.getMonth() + 1;
    const dia = fechaAhora.getDate() + 1;
    //formateo deseado aaaa--mm--dd
    const fechaDeshab = `${year}-${mes < 10 ? `0${mes}` : mes}-${dia < 10 ? `0${dia}` : dia}`;
    inputFecha.min = fechaDeshab;
}

function horaCita(){
    const inputHora=document.querySelector('#hora');
    inputHora.addEventListener('input', e=>{
        const horaCita=e.target.value;
        const hora=horaCita.split(':');
        if(hora[0]<10||hora>18){
            mostrarAlerta('Hora no validad');
            setTimeout(()=>{
              inputHora.value='';  
            },3000);
            
        }else{
            cita.hora=horaCita;
        }

    });
}